"use client";

import { useState } from "react";
import Navbar from "@/components/Navbar";
import HeroSection from "@/components/HeroSection";
import ProblemCapture from "@/components/ProblemCapture";
import PatternVisualizer from "@/components/PatternVisualizer";
import WarningDashboard from "@/components/WarningDashboard";
import CampusPulse from "@/components/CampusPulse";
import WisdomFeed from "@/components/WisdomFeed";
import Gamification from "@/components/Gamification";
import Testimonials from "@/components/Testimonials";
import Roadmap from "@/components/Roadmap";
import Footer from "@/components/Footer";
import type { College } from "@/lib/data";

export default function HomePage() {
  const [selectedCollege, setSelectedCollege] = useState<College | null>(null);

  return (
    <main className="min-h-screen">
      <Navbar />
      <HeroSection
        onCollegeSelect={setSelectedCollege}
        selectedCollege={selectedCollege}
      />
      <ProblemCapture />
      <PatternVisualizer />
      <WarningDashboard />
      <CampusPulse />
      <WisdomFeed />
      <Gamification />
      <Testimonials />
      <Roadmap />
      <Footer />
    </main>
  );
}
